.container {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 1fr;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;

  .container__left {
    min-width: 300px;
    padding: 10px;
    border-right: 3px dashed #3c4c5940;
  }

  .container__right {
    padding: 10px;
  }

  .title {
    margin-bottom: 20px;
  }
}
